:root {
	--color-white: #fff;
	--color-black: #000;
	--color-lightgray: #dfe2df;
	--color-gray: #9c9c9c;
	--color-blue: #6a83fb;

	--font-size-small: 14px;
	--font-size-medium: 16px;
	--font-family-main: sans-serif;
	--font-bold: 600;

	--transition: 0.4s;
}

*,
*:after,
*:before {
	box-sizing: border-box;
}

img {
	display: block;
}
